@import "mixins";
html,
body {
  height: 100%;
}
body {
  position: relative;
  min-width: 1024px;
  overflow: auto;
  @include body-typo;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

// Layout
.wrapper.-centered,
.wrapper.-start {
  position: relative;
  padding: 0;
  margin: 0 auto;
  min-width: 1024px;
  max-width: 1280px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  align-items: stretch;
}
.wrapper.-start {
  max-width: none;
  justify-content: flex-start;

  main.plato {
    width: 100%;
    max-width: none;

    > .container-fluid {
      max-width: 1440px;
      margin-right: 0;
      margin-left: 0;
    }
  }

  .page-title {
    margin-top: 10px;
  }
}

.sidebar {
  position: relative;
  flex-shrink: 1;
  z-index: 1000;
  display: block;
  margin: 0;
  width: auto;

  .sb-inner {
    width: 255px;
    position: relative;
    padding-bottom: 200px;

    .sb-footer {
      position: absolute;
      top: auto;
      bottom: 15px;
      left: 0;
      right: 0;
    }
  }

}

main.plato {
  position: relative;
  flex-shrink: 2;
  z-index: 1;
  display: block;
  width: auto;
  min-width: 769px;
  max-width: 1025px;
  padding: 0;
  margin: 0;

  .inwhite {
    display: block;
    margin: 0 0 15px 0;
    padding: 15px;
  }

  .row:last-of-type {

    .inwhite {
      margin-bottom: 15px;
    }
  }

  .row .inwhite .row:last-of-type {

    .inwhite {
      margin-bottom: 0;
    }
  }
}

main.plato {
  padding-top: 20px;
}

// Typography
h1, h2, h3, h4, h5, h6 {
  font-weight: 500;
  line-height: 1.25;

  &:first-child {
    margin-top: 0;
  }
}

h1 {
  @include font-size(24);
}
h2 {
  @include font-size(22);
}
h3 {
  @include font-size(18);
}

p,
ul,
ol {
  @include paragraph;

  &:last-of-type {
    margin-bottom: 0;
  }
}

ul,
ol {

  ul,
  ol {
    margin-top: 0;
  }
}

.fs15 {
  @include font-size(15);
}
.fs14 {
  @include font-size(14);
}
.fs12 {
  @include font-size(12);
}

// Buttons
.btn.-icon {
  position: relative;
  padding-right: 26px;

  .zmdi {
    position: absolute;
    top: 50%;
    right: 5px;
    @include font-size(20);
    line-height: .1;
    font-weight: bold;
    opacity: .4;
    transition: transform ease-in-out .1s;
  }

  &.selected,
  &[aria-expanded=true] {

    .zmdi-chevron-down {
      transform: rotate(180deg);
    }
  }

  &.btn-regular {

    .zmdi {
      opacity: .9;
    }
  }
}

.btn.-noborder {
  border-color: transparent;
}

.btn-input {
  @include btn-input;
}

.btn-flat {
  @include btn-flat;

  &.-nobg {
    background-color: transparent;
  }

  &:hover,
  &:focus {
    @include boxShadow(none);
  }
}

.btn-link {
  @include btn-link;
}

.btn-link .zmdi {
  display: inline-block;
  position: relative;
  top: .16ex;
  margin-left: .5ex;
}

*[class*=zmdi-long-arrow-] {
  display: inline-block;
  margin-left: .4ex;
  position: relative;
  top: .2ex;
}

